import sMineSortItem from "../../component/mine/s-mine-sort-item.vue"
import sMineListItem from "../../component/mine/s-mine-list-item.vue"
import sMineUnlogin from "../../component/mine/s-mine-unlogin.vue"
import sMineLogin from "../../component/mine/s-mine-login.vue"
export default {
	components: {
		sMineSortItem,
		sMineListItem,
		sMineUnlogin,
		sMineLogin
	},
	data() {
		return {
			sortList: [{
				name: "糗事",
				count: 0
			}, {
				name: "动态",
				count: 0
			}, {
				name: "评论",
				count: 0
			}, {
				name: "收藏",
				count: 0
			}],
			unLoginListData: [{
					icon: "icon-liulan",
					iconColor: "#44b3ff",
					title: "浏览历史"
				},
				{
					icon: "icon-huiyuanvip",
					iconColor: "#ff7434",
					title: "糗百认证"
				}
			],
			loginListData: [{
					icon: "icon-liulan",
					iconColor: "#44b3ff",
					title: "浏览历史",
					navigatorType: "navigatorTo",
					navigatorUrl: "/pages/mine/mine-scan-history/mine-scan-history"
				},
				{
					icon: "icon-huiyuanvip",
					iconColor: "#ff7434",
					title: "糗百认证"
				},
				{
					icon: "icon-icon_im_keyboard",
					iconColor: "#44b3ff",
					title: "审核糗事"
				}
			],
			isLogin: false,
			providerList: [],
			userInfo: {}
		}
	},
	methods: {
		getLoginChannel() {
			uni.getProvider({
				service: 'oauth',
				success: (result) => {
					this.providerList = result.provider.map((value) => {
						let providerName = '';
						let icon = ''
						switch (value) {
							case 'weixin':
								providerName = '微信登录'
								icon = 'weixin'
								break;
							case 'qq':
								providerName = 'QQ登录'
								icon = 'QQ'
								break;
							case 'sinaweibo':
								providerName = '新浪微博登录'
								icon = 'xinlangweibo'
								break;
						}
						return {
							name: providerName,
							id: value,
							loginIcon: icon
						}
					});
					// console.log(JSON.stringify(this.providerList))
				},
				fail: (error) => {
					console.log('获取登录通道失败', error);
				}
			});
		}
	},
	onNavigationBarButtonTap(e) {
		if (this.isLogin) {
			uni.navigateTo({
				url: "./mine-seeting/mine-seeting"
			})
		} else {
			uni.navigateTo({
				url: "mine-login/mine-login"
			})
		}
	},
	onLoad() {
		this.getLoginChannel()
	},
	onShow() {
		uni.getStorage({
			key: "userinfo",
			complete: (res) => {
				if (res.data == null || res.data == '') {
					this.isLogin = false
				} else {
					this.isLogin = true
					this.userInfo = JSON.parse(res.data)
					console.log(this.userInfo)
				}
			}
		})
	}
}
